  localsave() {

     this.wiftoaddress() ;

  if(this.network == 'testnet') {

     if(this.walletaddress != this.useraccount.testaddress) {
        this.presentAlert("", "Test address registered not matching ", "Error code:"+environment.err.W1230.code);
        return;
     }
     this.storage.set(environment.storageuniq+'walletwif', this.walletwif);
        this.presentAlert("", "", "Saved successsfully ");
  } else {
     if(this.walletaddress != this.useraccount.liveaddress) {
        this.presentAlert("", "Live address registered not matching ", "Error code:"  +environment.err.W1230.code);
        return;
     }
     this.storage.set(environment.storageuniq+'walletwif', this.walletwif);
        this.presentAlert("", "", "Saved successsfully ");
   }

}
  

